/* roboto-300normal - latin */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-display: swap;
  font-weight: 300;
  src:
    local('Roboto Light '),
    local('Roboto-Light'),
    url(fonts/roboto-latin-300.woff2) format('woff2'), 
    url(fonts/roboto-latin-300.woff) format('woff'); /* Modern Browsers */
}

